.confbox {
  display: grid;
  background-color: #f00;
  grid-area: 4/1/5/3;
}

@media (min-width: 481px) {
  .confbox {
    background-color: #f00;
    grid-area: 3/1/4/2;
    }
  }

@media (min-width: 1025px) {
  .confbox {
    background-color: #f00;
    grid-area: 2/3/3/4;
    }
  }
  